main{
 position: relative;
 top:57px;   
}
.ask-question {
    border-radius: 50%;
    background: #b8860b;
    border: 20px solid #f5deb3;
    box-shadow: 0px 0px 10px #5f9ea0;
    line-height: 0;
    position: fixed;
    width: 100px;
    height: 100px;
    bottom: 20px;
    right: 30px;
    -webkit-animation: ripple 2s infinite;
            animation: ripple 2s infinite;
}

.ask-question>span {
    color: white;
    font-size: 30px;
}

@-webkit-keyframes ripple {
    0% {
        box-shadow:  0 0 0 2em rgba(101, 255, 120, 0.3);
    }
    100% {
        box-shadow:  0 0 0 5em rgba(101, 255, 120, 0);
    }
}

@keyframes ripple {
    0% {
        box-shadow:  0 0 0 2em rgba(101, 255, 120, 0.3);
    }
    100% {
        box-shadow:  0 0 0 5em rgba(101, 255, 120, 0);
    }
}
.r-login-form{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.loading{
    display: flex;
    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    align-items: center;
    justify-content: center;
    background: black;
    opacity: 0.4;
}
.update-reg-details{
    margin: unset;
    max-width: 400px;
}
.router-link>a {
    color: inherit;
    text-decoration: unset;
}

.center-align {
    text-align: center;
}
